.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 25px;
  padding-left: 87px;
}
.header-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 283px;
    img {
      width: 100%;
    }
  }
  &__connection {
    display: flex;
    align-items: center;
    margin-left: 18px;
  }
  &__connection-group {
    display: flex;
    align-items: center;
  }
  &__tel {
    display: flex;
    align-items: center;
    &:hover {
      i {
        &:after {
          border: 1px solid #28920b;
        }
      }
      span {
        color: #28920b;
      }
    }
    i {
      flex-shrink: 0;
      max-width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 32px;
        height: 32px;
        background-color: rgba(40,146,11,0.0);
        border: 1px solid rgba(194,194,194,0.49);
        border-radius: 50%;
        transition: all 0.3s;
      }
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #000000;
      margin-left: 20px;
    }
  }
  &__email {
    display: flex;
    align-items: center;
    margin-left: 28px;
    &:hover {
      i {
        &:after {
          border: 1px solid #28920b;
        }
      }
      span {
        color: #28920b;
      }
    }
    i {
      flex-shrink: 0;
      max-width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 32px;
        height: 32px;
        background-color: rgba(40,146,11,0.0);
        border: 1px solid rgba(194,194,194,0.49);
        border-radius: 50%;
        transition: all 0.3s;
      }
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #000000;
      margin-left: 20px;
    }
  }
  &__hours {
    display: flex;
    align-items: center;
    margin-left: 28px;
    &:hover {
      i {
        &:after {
          border: 1px solid #28920b;
        }
      }
      span {
        color: #28920b;
      }
    }
    i {
      flex-shrink: 0;
      max-width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 32px;
        height: 32px;
        background-color: rgba(40,146,11,0.0);
        border: 1px solid rgba(194,194,194,0.49);
        border-radius: 50%;
        transition: all 0.3s;
      }
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #000000;
      margin-left: 20px;
    }
  }
  &__btn {
    width: 195px;
    height: 45px;
    margin-left: 10px;
  }
}
.header-bottom {
  margin-top: 20px;
  border-top: 1px solid #28920b;
  border-bottom: 1px solid #28920b;
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 17px 0;
    li {
      position: relative;
    }
    li:not(:first-child) {
      margin-left: 60px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -32px;
        transform: translate(0, -50%);
        width: 5px;
        height: 5px;
        background: #8e8e8e;
        border-radius: 50%;
      }
    }
  }
  &__link {
    font-size: 15px;
    line-height: 120%;
    font-weight: 700;
    &:hover {
      color: #28920b;
    }
  }
  &__item {
    position: relative;
    &--active {
      .header-bottom__item-show {
        span {
          color: #28920b;
        }
      }
      .header-bottom__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
      span {
        font-size: 15px;
        line-height: 120%;
        font-weight: 700;
      }
      i {
        margin-left: 5px;
        img {
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      &:hover {
        span {
          color: #28920b;
        }
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      min-width: 200px;
      width: 100%;
      background: #ffffff;
      box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
      padding: 15px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-link {
      display: block;
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      &:not(:first-child) {
        margin-top: 12px;
      }
      &:hover {
        color: #28920b;
      }
    }
  }
}
.header-mob {
  overflow: hidden;
  display: none;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 28px;
    padding: 5px 0;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 1500px;
      height: 1px;
      background: #28920b;
    }
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 1500px;
      height: 1px;
      background: #28920b;
    }
  }
  &__logo {
    display: block;
    width: 290px;
    img {
      width: 100%;
    }
  }
  &__burger {
    position: relative;
    z-index: 10;
    width: 40px;
    height: 30px;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: #28920b;
      border-radius: 5px;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 13px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 13px;
        transform: rotate(45deg);
      }
    }
  }
  &__tel {
    display: flex;
    align-items: center;
    &:hover {
      i {
        &:after {
          border: 1px solid #28920b;
        }
      }
      span {
        color: #28920b;
      }
    }
    i {
      flex-shrink: 0;
      max-width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 32px;
        height: 32px;
        background-color: rgba(40,146,11,0.0);
        border: 1px solid rgba(194,194,194,0.49);
        border-radius: 50%;
        transition: all 0.3s;
      }
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 400;
      color: #000000;
      margin-left: 20px;
    }
  }
  &__btn {
    width: 195px;
    height: 45px;
    margin-left: 25px;
  }
}

