@media screen and (max-width: 835px) {
  .header {
    padding-left: 0;
  }
  .header__wrapp {
    position: fixed;
    z-index: 9;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    background: #ffffff;
    transition: all 0.5s;
    padding: 70px 0 30px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
    &--active {
      left: 0;
    }
  }
  .header-top {
    &__inner {
      display: block;
    }
    &__logo {
      display: none;
    }
    &__connection {
      flex-direction: column;
      align-items: start;
      margin-top: 30px;
    }
    &__hours {
      margin: 16px 0 0 0;
    }
    &__btn {
      margin: 30px 0 0 0;
    }
  }
  .header-mob {
    display: block;
  }
  .header-bottom {
    margin-top: 30px;
    border: none;
    &__list {
      display: block;
      padding: 0;
      li:not(:first-child) {
        margin: 12px 0 0 0;
        &:after {
          display: none;
        }
      }
    }
    &__item {
      &--active {
        .header-bottom__item-hidden {
          display: block;
        }
      }
      &-hidden {
        min-width: 100%;
        position: static;
        background: transparent;
        box-shadow: unset;
        padding: 15px 0 15px 15px;
        opacity: 1;
        pointer-events: all;
        display: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header-mob {
    &__btn {
      margin-left: 10px;
    }
  }
}

@media screen and (max-width: 470px) {
  .header-mob {
    &__logo {
