.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-green {
  background-color: #28920b;
  border-radius: 5px;
  font-size: 15px;
  line-height: 120%;
  font-weight: 700;
  color: #ffffff;
  &:hover {
    box-shadow: 0px 10px 16px 0px rgba(40,146,11,0.34);
  }
}
.display2 {
  font-size: 52px;
  line-height: 120%;
  font-weight: 800;
  color: #000000;
  position: relative;
  span:nth-child(1) {
    color: #28920b;
  }
  span:nth-child(2) {
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px rgba(194,194,194,0.49);
    opacity: 0.4;
    position: absolute;
    top: -14px;
    left: -14px;
  }
  &:after {
    content: '';
    position: absolute;
    top: 28px;
    left: -170px;
    width: 125px;
    height: 6px;
    background: #28920b;
  }
}
.swiper-button-prev {
  width: 44px;
  height: 44px;
  border: 1px solid #c2c2c2;
  background: #ffffff;
  border-radius: 50%;
  img:nth-child(2) {
    transform: rotate(180deg);
    display: none;
  }
  &:after {
    display: none;
  }
  &:hover {
    border: 1px solid #28920b;
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
}
.swiper-button-next {
  width: 44px;
  height: 44px;
  border: 1px solid #c2c2c2;
  background: #ffffff;
  border-radius: 50%;
  img:nth-child(1) {
    transform: rotate(180deg);
  }
  img:nth-child(2) {
    display: none;
  }
  &:after {
    display: none;
  }
  &:hover {
    border: 1px solid #28920b;
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
}
.swiper-pagination {
  &-bullet {
    width: 10px;
    height: 10px;
    background: transparent;
    border: 2px solid #343434;
    opacity: 1;
  }
  &-bullet-active {
    background: #28920b;
    border: 2px solid transparent;
  }
}

@media screen and (max-width: 1500px) {
  .display2 {
    padding-left: 32px;
    &:after {
      left: -85px;
      width: 100px;
    }
    span:nth-child(2) {
      left: 18px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .display2 {
    font-size: 40px;
    &:after {
      top: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .display2 {
    font-size: 35px;
    &:after {
      top: 18px;
    }
  }
}

@media screen and (max-width: 835px) {
  .display2 {
    font-size: 30px;
    &:after {
      top: 14px;
    }
  }
}