.content {
  padding-left: 87px;
}
.sidebar {
  position: fixed;
  z-index: 3;
  width: 87px;
  height: 100vh;
  top: 0;
  left: 0;
  background: #000000;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 0;
  }
  &::-webkit-scrollbar-track {
    background: #000000;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #ffffff;
    border-radius: 3px;
  }
  &__top {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 91px;
    background-color: #2f443a;
  }
  &__burger {
    position: relative;
    z-index: 10;
    width: 28px;
    height: 21px;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: #ffffff;
      border-radius: 5px;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span {
        background: #28920b;
      }
      span:nth-child(1) {
        top: 9px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 9px;
        transform: rotate(45deg);
      }
    }
  }
  &__basket {
    max-width: 37px;
    display: flex;
    margin: 0 auto;
    padding: 42px 0 162px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 1px;
      height: 24px;
      background: #28920b;
    }
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 1px;
      height: 141px;
      background: #28920b;
    }
  }
  &__basket-inner {
    position: relative;
    span {
      position: absolute;
      top: -8px;
      right: -10px;
      width: 19px;
      height: 19px;
      background: #28920b;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 10px;
      line-height: 105%;
      font-weight: 600;
      color: #ffffff;
    }
  }
  &__socials {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
    margin-top: 27px;
  }
  &__social {
    max-width: 26px;
    margin: 0 auto;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 158px 0 28px;
    margin-top: 27px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 1px;
      height: 28px;
      background: #28920b;
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 1px;
      height: 158px;
      background: #28920b;
    }
  }
  &__btn {
    width: 51px;
    height: 254px;
    background: transparent;
    border: 2px solid #28920b;
    &:hover {
      background: #28920b;
    }
    span {
      font-size: 12px;
      line-height: 105%;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.06em;
      color: #ffffff;
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    &--active {
      opacity: 1;
      pointer-events: all;
      .sidebar__menu-inner {
        margin-left: 0;
      }
    }
  }
  &__menu-inner {
    width: 400px;
    height: 100%;
    background: #ffffff;
    padding: 100px 15px 30px;
    margin-left: -105%;
    transition: all 0.5s;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
  }
  &__connection {
    padding-left: 8px;
  }
  &__tel {
    display: flex;
    align-items: center;
    &:hover {
      i {
        &:after {
          border: 1px solid #28920b;
        }
      }
      span {
        color: #28920b;
      }
    }
    i {
      flex-shrink: 0;
      max-width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 32px;
        height: 32px;
        background-color: rgba(40,146,11,0.0);
        border: 1px solid rgba(194,194,194,0.49);
        border-radius: 50%;
        transition: all 0.3s;
      }
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #000000;
      margin-left: 20px;
    }
  }
  &__email {
    display: flex;
    align-items: center;
    margin-top: 28px;
    &:hover {
      i {
        &:after {
          border: 1px solid #28920b;
        }
      }
      span {
        color: #28920b;
      }
    }
    i {
      flex-shrink: 0;
      max-width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 32px;
        height: 32px;
        background-color: rgba(40,146,11,0.0);
        border: 1px solid rgba(194,194,194,0.49);
        border-radius: 50%;
        transition: all 0.3s;
      }
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #000000;
      margin-left: 20px;
    }
  }
  &__hours {
    display: flex;
    align-items: center;
    margin-top: 28px;
    &:hover {
      i {
        &:after {
          border: 1px solid #28920b;
        }
      }
      span {
        color: #28920b;
      }
    }
    i {
      flex-shrink: 0;
      max-width: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 32px;
        height: 32px;
        background-color: rgba(40,146,11,0.0);
        border: 1px solid rgba(194,194,194,0.49);
        border-radius: 50%;
        transition: all 0.3s;
      }
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #000000;
      margin-left: 20px;
    }
  }
  &__menu-btn {
    width: 195px;
    height: 45px;
    margin-top: 28px;
  }
  &__list {
    margin-top: 28px;
    li {
      position: relative;
    }
    li:not(:first-child) {
      margin-top: 15px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -32px;
        transform: translate(0, -50%);
        width: 5px;
        height: 5px;
        background: #8e8e8e;
        border-radius: 50%;
      }
    }
  }
  &__link {
    font-size: 15px;
    line-height: 120%;
    font-weight: 700;
    &:hover {
      color: #28920b;
    }
  }
  &__item {
    position: relative;
    &--active {
      .header-bottom__item-show {
        span {
          color: #28920b;
        }
      }
      .header-bottom__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
      span {
        font-size: 15px;
        line-height: 120%;
        font-weight: 700;
      }
      i {
        margin-left: 5px;
        img {
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      &:hover {
        span {
          color: #28920b;
        }
      }
    }
    &-hidden {
      padding: 15px 0 15px 15px;
      display: none;
    }
    &-link {
      display: block;
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      &:not(:first-child) {
        margin-top: 12px;
      }
      &:hover {
        color: #28920b;
      }
    }
  }
}
